<template>
  <div>
    <div class="row mr-0">
      <div class="row flex xs12 sm6 align--center">
        <div class=" flex md6 sm12 xs12 px-1 text-start">
          <label class="input-label"> {{$t('placeholder.ChooseRoute')}} </label>

          <va-select-c
            v-model="routsSelect.value"
            searchable
            textBy="RouteName"
            keyBy="id"
            :options="routsSelect.options"
          />
        </div>
        <div class="flex md6 sm12 mt-4 xs12 px-1 text-start">
          <va-checkbox
            :label="$t('placeholder.showTrafic')"
            v-model="showTrafic"
          />
        </div>
      </div>
    </div>
    <div class="google-map map-fill-height" ref="googleMap" />
    <va-modal v-model="createModal" no-outside-dismiss
      fullscreen
      hide-default-actions
      :title="$t('generic.Detailes') +' ' + routeName "
      :cancelText=" $t('modal.cancel')">
      <div v-if="loading" class="flex-center spinner-box">
        <SpringSpinner
          :animation-duration="1500"
          :color="this.$themes.primary"
          :size="80"
        >
        </SpringSpinner>
      </div>
      <div  v-else>
        <div class="row justify--space-between">
          <span>الوقت المتوقع <span>{{expextedTime}}</span></span>
          <span>المسافة<span>{{distance}}</span></span>
          <!-- <span>{{timeNow - expextedTime >= 3 ? '' :''}}</span> -->
          <span>الوقت الان <span>{{timeNow}}</span></span>

        </div>
        <div class="row flex-center">

          <div id="map" class="google-map  fill-height" ref="googleMapDetails" style="height: 69vh;"/>
          <div id="sidebar">
            <div id="panel"></div>
          </div>
        </div>
        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="createModal = false">
            {{ $t('modal.cancel') }}
          </va-button>
        </div>
      </div>
    </va-modal>
  </div>
</template>

<script>
import * as GoogleMapsLoader from 'google-maps'
import firebase from 'firebase'
import * as spinners from 'epic-spinners'
import axios from 'axios'

export default {
  name: 'google-map',
  data () {
    return {
      routeName: '',
      timeNow: '',
      distance: '',
      expextedTime: '',
      routes: [],
      routsSelect: {
        value: '',
        options: [],
      },
      departureTime: null,
      iconPr: 'https://i.ibb.co/zScnP9L/IconPr.gif',
      iconSc: 'https://i.ibb.co/4t5K1jd/IconSec.gif',
      loading: true,
      createModal: false,
      googleMap: null,
      smap: null,
      showTrafic: false,
      trafficLayer: null,
      markers: [],
      IW: null,
    }
  },
  props: {
    time: Number,
  },
  components: {
    ...spinners,
  },
  async mounted () {
    await this.PageRender()
  },
  methods: {
    async PageRender () {
      const self = this
      const time = Number(this.time)
      if (time > 0) {
        var newTimeAfter = new Date()
        newTimeAfter.setMinutes(newTimeAfter.getMinutes() + time)
        this.departureTime = newTimeAfter.toISOString()
      }
      GoogleMapsLoader.KEY = 'AIzaSyC6Wt_40qWZ7up5hvZi9GwzxYWmwjnXggs'
      GoogleMapsLoader.VERSION = '3.35'
      await GoogleMapsLoader.load(async (google) => {
        this.googleMap = google
        self.smap = new google.maps.Map(this.$refs.googleMap, {
          center: new google.maps.LatLng(24.774265, 46.738586),
          zoom: 11,
          mapTypeId: google.maps.MapTypeId.ROADMAP,
        })
        self.IW = new google.maps.InfoWindow()
        self.trafficLayer = new google.maps.TrafficLayer()
        const firestore = firebase.firestore()
        await firestore.collection('routes').get().then(async (querySnapshot) => {
          querySnapshot.forEach((doc, index) => {
            var obj = doc.data()
            obj.id = doc.id
            self.routes.push(obj)
            var LongLat = obj.FromLocation.split(',')
            var org = new google.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
            var LongLatDes = obj.toLocation.split(',')
            var destination = new google.maps.LatLng(Number(LongLatDes[0]), Number(LongLatDes[1]))
            axios
              .post('http://34.78.114.66:9090/routes/computeRouteWithTrafficLength', {
                origin: org,
                destination: destination,
                waypoints: obj.waypoints,
                departureTime: self.departureTime,
              })
              .then((resp) => {
                console.log('data', resp.data)
                self.render(resp.data[0], doc.id)
                const mo = new google.maps.Marker({
                  position: new google.maps.LatLng(org),
                  map: self.smap,
                  title: obj.RouteName,
                  animation: google.maps.Animation.DROP,
                  icon: 'http://maps.google.com/mapfiles/kml/paddle/A.png',
                })
                mo.addListener('click', (eve) => {
                  self.showIW(obj, eve.latLng)
                })
                self.markers.push(mo)
                // eslint-disable-next-line no-new
                const md = new google.maps.Marker({
                  position: new google.maps.LatLng(destination),
                  map: self.smap,
                  title: obj.RouteName,
                  animation: google.maps.Animation.DROP,
                  icon: 'http://maps.google.com/mapfiles/kml/paddle/B.png',
                })
                md.addListener('click', (eve) => {
                  self.showIW(obj, eve.latLng)
                })
                self.markers.push(md)
              })
              .catch(err => {
                console.error(err)
              })
          })
        })
      })
    },
    render (route, id) {
      const self = this
      const decodedPath = route.polyline.geoJsonLinestring.coordinates
      const decodedPathLatLng = decodedPath.map(elt => { return { lat: elt[1], lng: elt[0] } })
      const speedReadingIntervals = route.travelAdvisory?.speedReadingIntervals || []
      var Routeindex = this.routes.findIndex(x => x.id === id)
      if (Routeindex > -1) {
        this.routes[Routeindex].poly = []
        this.routes[Routeindex].ApiData = route
      } else {
        console.error(`can not find route with id ${id}`)
      }
      speedReadingIntervals.forEach(interval => {
        // console.log(`interval`, interval);
        const startIndex = interval.startPolylinePointIndex || 0
        const endIndex = interval.endPolylinePointIndex || 0
        const type = interval.speed
        let poly = decodedPathLatLng.slice(startIndex, endIndex + 1)
        var colors = { TRAFFIC_JAM: '#d74342', SLOW: '#e69e01', NORMAL: '#4078c9' }
        poly = new this.googleMap.maps.Polyline({
          path: poly,
          strokeColor: colors[type],
          geodesic: true,
          strokeOpacity: 1,
          strokeWeight: 6,
        })
        this.googleMap.maps.event.addListener(poly, 'click', function (evt) {
          self.showIW(self.routes[Routeindex], evt.latLng)
        })
        poly.setMap(self.smap)
        if (Routeindex > -1) {
          this.routes[Routeindex].poly.push(poly)
        }
      })
    },
    showIW (route, position) {
      const data = route?.ApiData
      let html = ''
      html += '<div dir="ltr">'
      html += `<span class="${route.iSPrimary ? 'infotitlePr' : 'infotitleSc'}">${route.RouteName}</span><br/>`
      html += `<span class="infosegment">المسافة: ${data.statistics.distance.value / 1000} كم </span><br/>`
      html += `<span class="infosegment"> الوقت بدون زحام: ${Math.round(route.DeafultTime / 60)} دقيقة</span><br/>`
      html += `<span class="infosegment"> الوقت الان: ${this.fromatTime(data.statistics.duration.value, true)}</span><br/>`

      html += '<br/>'
      html += `<span class="infonormal">اعتيادي: ${data.statistics.normal.distance.value / 1000} كم (${data.statistics.normal.distance.percentage})</span><br/>`
      html += `<span class="infoslow">بطئ: ${data.statistics.slow.distance.value / 1000} كم  (${data.statistics.slow.distance.percentage})</span><br/>`
      html += `<span class="infojam">مزدحم: ${data.statistics.jam.distance.value / 1000} كم  (${data.statistics.jam.distance.percentage})</span><br/>`
      html += '</div>'

      if (this.IW) {
        this.IW.setContent(html)
        this.IW.setPosition(position)
        this.IW.open({ map: this.smap })
      }

      console.log('showin poly click', route)
    },
    fromatTime (time, isSec = false) {
      let text

      const durationInSec = isSec ? parseInt(time) : parseInt(time) * 60

      if (durationInSec < 60) text = `${durationInSec}ثانية`
      text = `${Math.round(durationInSec / 60)} دقيقة`
      return text
    },
    renderAll () {
      const self = this
      this.routes.forEach(el => {
        if (el.poly) {
          el.poly.forEach(stepPoly => {
            // eslint-disable-next-line no-unused-expressions
            stepPoly?.setMap(this.smap)
          })
        }
        var LongLat = el.FromLocation.split(',')
        var org = new this.googleMap.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
        var LongLatDes = el.toLocation.split(',')
        var destination = new this.googleMap.maps.LatLng(Number(LongLatDes[0]), Number(LongLatDes[1]))
        const mo = new this.googleMap.maps.Marker({
          position: new this.googleMap.maps.LatLng(org),
          map: this.smap,
          title: el.RouteName,
          animation: this.googleMap.maps.Animation.DROP,
          icon: 'http://maps.google.com/mapfiles/kml/paddle/A.png',
        })
        mo.addListener('click', (eve) => {
          self.showIW(el, eve.latLng)
        })

        this.markers.push(mo)
        // eslint-disable-next-line no-new
        const md = new this.googleMap.maps.Marker({
          position: new this.googleMap.maps.LatLng(destination),
          map: this.smap,
          title: el.RouteName,
          animation: this.googleMap.maps.Animation.DROP,
          icon: 'http://maps.google.com/mapfiles/kml/paddle/B.png',
        })
        md.addListener('click', (eve) => {
          self.showIW(el, eve.latLng)
        })
        this.markers.push(md)
      })
    },
    DeletAll () {
      this.routes.forEach(el => {
        if (el.poly) {
          el.poly.forEach(stepPoly => {
             // eslint-disable-next-line no-unused-expressions
             stepPoly?.setMap(null)
          })
        }
      })
      this.markers.forEach(m => {
        // eslint-disable-next-line no-unused-expressions
        m?.setMap(null)
      })
    },
    drawOnlyRoute (route) {
      const self = this

      if (route.poly) {
        for (let i = 0; i < route.poly.length; i++) {
          route.poly[i].setMap(this.smap)
        }
        var LongLat = route.FromLocation.split(',')
        var org = new this.googleMap.maps.LatLng(Number(LongLat[0]), Number(LongLat[1]))
        var LongLatDes = route.toLocation.split(',')
        var destination = new this.googleMap.maps.LatLng(Number(LongLatDes[0]), Number(LongLatDes[1]))
        const mo = new this.googleMap.maps.Marker({
          position: new this.googleMap.maps.LatLng(org),
          map: this.smap,
          title: route.RouteName,
          animation: this.googleMap.maps.Animation.DROP,
          icon: 'http://maps.google.com/mapfiles/kml/paddle/A.png',
        })
        mo.addListener('click', (eve) => {
          self.showIW(route, eve.latLng)
        })
        this.markers.push(mo)
        // eslint-disable-next-line no-new
        const md = new this.googleMap.maps.Marker({
          position: new this.googleMap.maps.LatLng(destination),
          map: this.smap,
          title: route.RouteName,
          animation: this.googleMap.maps.Animation.DROP,
          icon: 'http://maps.google.com/mapfiles/kml/paddle/B.png',
        })
        md.addListener('click', (eve) => {
          self.showIW(route, eve.latLng)
        })
        this.markers.push(md)
      }
    },
  },
  watch: {
    showTrafic: function () {
      if (this.trafficLayer != null) {
        this.showTrafic ? this.trafficLayer.setMap(this.smap) : this.trafficLayer.setMap(null)
      }
    },
    routes: function () {
      this.routsSelect.options = this.routes
    },
    time: function () {
      this.routsSelect.value = ''
      this.PageRender()
    },
    'routsSelect.value': function () {
      if (this.routsSelect.value) {
        this.DeletAll()
        this.drawOnlyRoute(this.routsSelect.value)
      } else {
        this.DeletAll()
        this.renderAll()
      }
      console.log('routsSelect.value change', this.routsSelect.value)
    },
  },
}
</script>

